<template>
    <el-menu class="el-menu-demo" mode="horizontal">
        <el-menu-item index="1">Processing Center</el-menu-item>
        <el-sub-menu index="2">
            <template #title>Workspace</template>
            <el-menu-item index="2-1">item one</el-menu-item>
            <el-menu-item index="2-2">item two</el-menu-item>
            <el-menu-item index="2-3">item three</el-menu-item>
            <el-sub-menu index="2-4">
                <template #title>item four</template>
                <el-menu-item index="2-4-1">item one</el-menu-item>
                <el-menu-item index="2-4-2">item two</el-menu-item>
                <el-menu-item index="2-4-3">item three</el-menu-item>
            </el-sub-menu>
        </el-sub-menu>
        <el-button type="primary" @click="handleLogout">Wyloguj</el-button>
    </el-menu>
</template>
<script>
import axios from 'axios'
import { ElMessage } from 'element-plus'
import { h } from 'vue'
import { ElNotification } from 'element-plus'
export default {
    name: 'AuthLayout',
    created() {
        this.checkAuthenticated()
    },
    methods: {
        checkAuthenticated() {
            const token = localStorage.getItem('token')
            if (!token) {
                ElMessage.error('Zaloguj się, aby uzyskać dostęp do tej strony')
                this.$router.push('/')
                return
            }
            const headers = {
                'Authorization': `Token ${token}`,
                'Content-Type': 'application/json' // Możesz dostosować typ zawartości w zależności od potrzeb
            }
            axios.get('http://127.0.0.1:8000/api/userdata/', { headers })
                .then((response) => {
                    console.log(response.data)
//                    const username = response.data.username
                    const name =  response.data.name
                    const surname = response.data.surname
//                    const email = response.data.email

                    ElNotification({
                        title: `${name} ${surname}`,
                        message: h('i', { style: 'color: blue' }, 'Witamy'),
                    })
                })
                .catch(error => {
                    // Obsłuż błąd żądania
                    console.error('Błąd podczas sprawdzania statusu logowania:', error)
                })

        },
        handleLogout() {
            const token = localStorage.getItem('token')
            const logoutUrl = 'http://127.0.0.1:8000/api/logout/'

            const headers = {
                'Authorization': `Token ${token}`,
                'Content-Type': 'application/json'
            }
            axios.post(logoutUrl, null, { headers })
                .then(response => {
                    console.log(response.data.message)
                    localStorage.removeItem('token')
                    this.$router.push('/')
                })
                .catch(error => {
                    // Obsługa błędu
                    console.error('Błąd podczas wylogowywania:', error)
                })

        }
    }
}
</script>